* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #070F2B;
  color: white;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

nav {
  position: relative;
  top: 0;
  margin: 0;
  margin-bottom: 10px;
  padding: 10px;
  width: 100vw;
  display: flex;
  flex-direction: row;
  flex-flow: row;
  justify-content: left;
  align-items: left;
  background-color: #1B1A55;
  /* border-bottom: 2px solid #606060; */
}

h1 {
  width: fit-content;
  font-size: 3em;
  margin: 0.5em;
  padding: 0.5em;
  background-color: #4156cc;
  border: 2px solid white;
  box-shadow: 0 0 4px rgba(255, 255, 255, 0.5);
}

h2 {
  margin: 10px;
}

#newest {
  font-size: 2em;
  animation: soft-glow 2s alternate infinite cubic-bezier(0.5, 0, 0.5, 1);
}

@keyframes soft-glow {
  0% {
    text-shadow: 0 0 8px rgba(255, 255, 255, 0.5);
  }
  100% {
    text-shadow: 0 0 20px rgba(255, 255, 255, 0.75);
  }
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-flow: column;
  margin: 0;
}

.section-container {
  position: relative;
  text-align: center;
  background-color: #1B1A55;
  margin: 10px;
  padding: 10px;
  border-radius: 10px;
}

.section-collapsed {
  overflow: hidden;
}

.section-expand-arrow {
  position: absolute;
  width: 32px;
  height: 20px;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0,0,0, 50%);
  padding: 12px 6px 12px 6px;
  border-radius: 50%;
  cursor: pointer;
}

.section-expand-arrow > svg > path {
  position: absolute;
  transform-origin: center center;
  transition: 200ms ease-in-out;

}

.row {
  display: flex;
  flex-direction: row;
  flex-flow: row;
}

.music-section {
  width: calc(90% - 20px);
  margin: 10px auto;
}

iframe {
  margin: 10px;
}

.soundcloud {
  text-align: center;
  display: flex;
  flex-direction: row;
  flex-flow: row;

}

.soundcloud-links {
  background-color: #2849db;
  color: white !important;
  text-decoration: none;
  margin: 10px;
  font-size: 1.5em;
  padding: 16px;
  border-radius: 12px;
  transition: 300ms ease-in-out;
}


.soundcloud-links:hover {
  background-color: #2d53f8;

}

.soundcloud-links:active {
  background-color: #223ebb;

}
