footer {
  height: 100px;
  border-top: 1px solid #606060;
  margin-top: 80px;
}

#footer-text {
  font-size: 0.75em;
  color: #888;
  margin: 20px;
}

a {
  color: #438bf0;
  text-decoration: none;
}

a:visited {
  color: #5443f0;
}
